import { Component, OnInit } from '@angular/core';
import { MatBottomSheetRef } from '@angular/material';

@Component({
  selector: 'app-massage-bottom-sheet',
  templateUrl: './massage-bottom-sheet.component.html',
  styleUrls: ['./massage-bottom-sheet.component.css']
})
export class MassageBottomSheetComponent implements OnInit {
  today: Date;
  constructor(
    private bottomSheetRef: MatBottomSheetRef<MassageBottomSheetComponent>
  ) { }

  ngOnInit() {
    this.today = new Date();
  }

  openLink(event: MouseEvent): void {
    this.bottomSheetRef.dismiss();
    event.preventDefault();
  }

  sendMessage() {

  }
}
