import { AddFriendDialogComponent } from './add-friend-dialog/add-friend-dialog.component';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Message } from 'src/app/main/models';
import { NavService } from '../servcies/nav.service';
import { CommunicationService } from './service/communication.service';
import { MatDialog } from '@angular/material';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-communication',
  templateUrl: './communication.component.html',
  styleUrls: ['./communication.component.css']
})
export class CommunicationComponent implements OnInit {
  @Input() searchWord: string;
  today = new Date();
  nowT = new Date(Date.parse(new Date().toString()));
  nowTStr = this.getNowTH() + ':' + this.getNowTM() + ':' + this.getNowTS() + ' ' + this.getNowT();
  messages: Message[] = [];
  public form: FormGroup;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private nav: NavService,
    private communication: CommunicationService,
    public dialog: MatDialog,
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      search: ['']
    });
    this.route.url.subscribe(res => this.nav.setUrl(res[0].path));
    this.getMessages();
  }
  get search() { return this.form.get('search'); }

  searched() {
    console.log(this.searchWord);
  }

  getMessages() {
    this.communication.getMessages()
      .subscribe(messages => this.messages = messages);
  }

  getNowTH() {
    let nowTH = this.nowT.getHours();
    if (nowTH > 12) {
      nowTH = nowTH - 12;
    }
    if (nowTH >= 10) {
      return nowTH;
    } else {
      return '0' + nowTH;
    }
  }

  getNowTM() {
    const nowTM = this.nowT.getMinutes();
    if (nowTM >= 10) {
      return nowTM;
    } else {
      return '0' + nowTM;
    }
  }

  getNowTS() {
    const nowTS = this.nowT.getSeconds();
    if (nowTS >= 10) {
      return nowTS;
    } else {
      return '0' + nowTS;
    }
  }

  getNowT() {
    const nowTH = this.nowT.getHours();
    let now = '早上';
    if (nowTH > 12) {
      now = '下午';
      return now;
    } else {
      return now;
    }
  }

  addFriendDialog() {
    const dialogRef = this.dialog.open(AddFriendDialogComponent, {
      width: '600px',
      data: {
      }
    });
    dialogRef.afterClosed().subscribe(res => {
        if (res) {
            console.log(res);
            // this.travel.addPlace(res).subscribe(res2 => {
            //   if (res2) {
            //     this.snackbar.open('新增成功', 'OK', { duration: 3000});
            //     this.ngOnInit();
            //     // this.router.navigate(['/personal/account']);
            //     // this.userService.logout();
            //     // this.router.navigate(['/user/login']);
            //   } else {
            //     this.snackbar.open('新增失敗', 'OK', {duration: 3000});
            //   }
            // });
        }
    });
  }

}
